import React, {
	Component
} from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Consumer } from '../../context';
import axios from 'axios';

class Contact extends Component {

	state = {
		showContactInfo: false
	}

	onDeleteClick = async (id, dispatch) => {
		await axios.delete(`https://jsonplaceholder.typicode.com/users/${id}`);
		dispatch({type: 'DELETE_CONTACT', payload: id})
	}

	// static propTypes = {
	// 	name: PropTypes.string.isRequired,
	// 	email: PropTypes.string.isRequired,
	// 	phone: PropTypes.string.isRequired,
	// }

	render() {
		const { id, name, email, phone } = this.props.contact;
		const { showContactInfo } = this.state;

		return ( 
			<Consumer>
				{value => {
					const { dispatch } = value;
					return (
						<div className="card card-body mb-3">
							<h4>{name} <i style={{cursor: 'pointer'}} onClick={() => (
								this.setState({showContactInfo: !this.state.showContactInfo})
							)} className="fas fa-sort-down"></i>
								<i onClick={this.onDeleteClick.bind(this, id, dispatch)} className="fa fa-times" style={{color: 'red', cursor: 'pointer', float: 'right'}}></i>
								<Link to={`/contact/edit/${id}`}>
									<i className="fa fa-pencil-alt" style={{
										cursor: 'pointer',
										color: 'black',
										marginRight: '1rem',
										float: 'right'
									}}></i>
								</Link>
							</h4>

							{showContactInfo ? (
								<ul className="list-group">
								<li className="list-group-item">Phone : {phone}</li>
								<li className="list-group-item">Email : {email}</li>
							</ul>
							) : null}
							
						</div>
					)
				}}
			</Consumer>
		)
	}
}

Contact.propTypes = {
	contact: PropTypes.object.isRequired,
}

export default Contact;